/* GLOBAL FONT STYLES */
p, .is-bold, strong, .is-italic, em, .content, h1, h2, h3, h4, h5 {
  font-family: poppins, sans-serif !important; }

p.legend {
  font-style: italic;
  color: #7b7b7b; }

.is-bold, strong {
  font-weight: bold !important; }

.is-italic, em {
  font-style: italic !important; }

.centered, em {
  text-align: center; }

a, h1, h2, h3, h4, h5 {
  color: #0f2f57;
  transition: color .3s; }
  a:hover, h1:hover, h2:hover, h3:hover, h4:hover, h5:hover {
    color: #0087bd; }

a {
  text-decoration: none; }

ul li a {
  position: relative; }
  ul li a::before {
    content: '';
    display: block;
    position: absolute;
    bottom: -1px;
    left: 50%;
    transform: translateX(-50%);
    width: 0;
    height: 2px;
    background: #0087bd;
    transition: .3s ease; }
  ul li a:hover::before {
    width: 80%; }

li {
  list-style-type: none;
  font-family: poppins, sans-serif !important; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

.Typist {
  font-size: 2.5rem;
  color: white;
  letter-spacing: 2px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  text-align: center;
  font-family: open24 !important; }
  @media (max-width: 599px) {
    .Typist {
      font-size: 1.5rem;
      margin: 0; } }
  .Typist .Cursor {
    padding-left: 1rem;
    vertical-align: middle;
    color: white;
    font-family: Helvetica, sans-serif;
    animation-name: blinker .7s linear infinite; }

