#graphism-wrapper {
  height: 100%;
  width: 100%;
  position: relative; }
  #graphism-wrapper #graphism {
    overflow-y: scroll !important;
    height: 90vh;
    width: 93vw;
    margin: 0 auto;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-evenly;
    align-items: center;
    justify-content: space-evenly;
    z-index: 5; }
    #graphism-wrapper #graphism article.graphic {
      width: 328px;
      height: 225px;
      margin: .5rem .25rem;
      overflow: hidden;
      box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.6);
      border-radius: 5px;
      background: #0f2f57;
      position: relative;
      cursor: pointer; }
      #graphism-wrapper #graphism article.graphic h2 {
        font-size: 2em;
        padding: 1rem;
        color: white;
        position: absolute;
        top: -250px;
        width: 100%;
        left: 0;
        text-transform: uppercase;
        text-shadow: 1px 1px #000;
        text-align: center;
        transition: .3s ease; }
      #graphism-wrapper #graphism article.graphic p.description {
        display: none; }
      #graphism-wrapper #graphism article.graphic img {
        min-height: 100%;
        min-width: 100%;
        height: auto;
        width: 125%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 1;
        transition: all .8s ease;
        filter: blur(0) brightness(1); }
      #graphism-wrapper #graphism article.graphic:hover h2 {
        top: 35px; }
      #graphism-wrapper #graphism article.graphic:hover img {
        opacity: 0.4;
        filter: blur(2px) brigthness(0.4); }
  #graphism-wrapper #gallery-fullscreen {
    position: fixed;
    height: calc(100% - 50px);
    width: 100%;
    overflow: hidden;
    background: rgba(15, 48, 87, 0.95);
    z-index: 10;
    top: 50px; }
    #graphism-wrapper #gallery-fullscreen section.selected img {
      width: auto;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      max-width: 80vw;
      max-height: 70vh;
      object-fit: contain; }
    #graphism-wrapper #gallery-fullscreen section.selected header h3 {
      color: white;
      font-size: 2rem;
      text-shadow: 1px 2px 4px rgba(0, 0, 0, 0.8);
      text-transform: uppercase;
      text-align: center;
      width: 100%;
      position: absolute;
      top: 1rem; }
    #graphism-wrapper #gallery-fullscreen section.selected p {
      display: none; }
    #graphism-wrapper #gallery-fullscreen nav .nav {
      position: absolute;
      top: 50%;
      transform: translateY(-50%) translateZ(100px);
      cursor: pointer; }
      #graphism-wrapper #gallery-fullscreen nav .nav .close svg {
        fill: white; }
      #graphism-wrapper #gallery-fullscreen nav .nav.prev, #graphism-wrapper #gallery-fullscreen nav .nav.next {
        color: white;
        width: 46px;
        height: 48px; }
      #graphism-wrapper #gallery-fullscreen nav .nav.prev {
        left: 0; }
      #graphism-wrapper #gallery-fullscreen nav .nav.next {
        right: 0;
        transform: translateY(-50%) rotateY(180deg); }
