#videos-container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 1rem 0; }
  #videos-container .video-details, #videos-container .next {
    padding: 1rem;
    padding-top: .5rem; }
  #videos-container .video-details {
    width: 70vw; }
    #videos-container .video-details .screen {
      width: 60vw;
      max-width: 1280px;
      display: flex; }
      #videos-container .video-details .screen iframe {
        width: 70vw;
        height: 70vh;
        max-height: 60vh;
        margin: 0 auto; }
    #videos-container .video-details .infos {
      padding-top: 1rem; }
      #videos-container .video-details .infos h3 {
        font-size: 2.5rem;
        text-transform: uppercase; }
      #videos-container .video-details .infos p {
        padding: 0 1rem 0 1rem;
        max-height: 80px;
        overflow: auto; }
  #videos-container .next {
    height: 80vh;
    min-width: 25vw;
    overflow-y: scroll; }
    #videos-container .next .video-preview {
      margin: 0 0 02rem;
      cursor: pointer;
      box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.4);
      padding: .5rem; }
      #videos-container .next .video-preview .cover {
        width: 100%; }
