#works {
  position: relative;
  width: 100%;
  height: calc(100vh - 50px);
  overflow: hidden; }
  #works .quarter {
    position: absolute;
    width: 50%;
    height: 50%;
    background: rgba(0, 0, 0, 0.2);
    transition: .3s;
    z-index: 2;
    overflow: hidden; }
    #works .quarter:nth-of-type(1) {
      top: 0;
      left: 0;
      border-right: 1px solid white;
      border-bottom: 1px solid white;
      animation: slideLeft 1s ease; }
    #works .quarter:nth-of-type(2) {
      top: 0;
      left: 50%;
      border-left: 1px solid white;
      border-bottom: 1px solid white;
      animation: slideDown 1s ease; }
      #works .quarter:nth-of-type(2).full {
        left: 0; }
    #works .quarter:nth-of-type(3) {
      top: 50%;
      left: 0;
      border-right: 1px solid white;
      border-top: 1px solid white;
      animation: slideUp 1s ease; }
      #works .quarter:nth-of-type(3).full {
        top: 0; }
    #works .quarter:nth-of-type(4) {
      top: 50%;
      left: 50%;
      border-left: 1px solid white;
      border-top: 1px solid white;
      animation: slideRight 1s ease; }
      #works .quarter:nth-of-type(4).full {
        top: 0;
        left: 0; }
    #works .quarter header {
      width: 100%;
      height: 100%;
      position: relative;
      cursor: pointer; }
      #works .quarter header .Typist {
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        transition: .6s; }
      #works .quarter header .category-icon {
        position: absolute;
        top: 30%;
        left: 50%;
        transform: translateX(-50%);
        opacity: 0;
        transition: .4s;
        width: 68px;
        height: 68px;
        color: white; }
    #works .quarter:hover {
      background: rgba(0, 0, 0, 0.4); }
      #works .quarter:hover header .Typist {
        top: 60%; }
      #works .quarter:hover header .category-icon {
        transition: .8s;
        opacity: 1; }
    #works .quarter section.content {
      display: none;
      opacity: 0;
      animation: fadeIn 1s .3s forwards; }
    #works .quarter .close {
      position: absolute;
      top: 15px;
      right: 15px;
      font-size: 2rem;
      font-weight: bold;
      cursor: pointer;
      z-index: 5;
      width: 32px;
      height: 32px;
      color: #0f2f57; }
      #works .quarter .close.white {
        color: white; }
    #works .quarter.full {
      width: 100%;
      height: 100%;
      background: whitesmoke;
      z-index: 10;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center; }
      #works .quarter.full header {
        height: 0; }
        #works .quarter.full header .Typist, #works .quarter.full header .category-icon {
          display: none; }
      #works .quarter.full .content {
        display: flex;
        justify-content: center;
        height: 90vh;
        width: 90vw; }
