nav#main-nav {
  position: fixed;
  top: 0;
  height: 50px;
  width: 100%;
  background: white;
  color: #0f2f57;
  padding: 0 2rem;
  z-index: 8;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.6); }
  nav#main-nav h3 {
    font-size: 2rem; }
    nav#main-nav h3 span {
      max-width: 0;
      transition: all .6s ease;
      display: inline-block;
      overflow: hidden;
      vertical-align: bottom;
      opacity: 0;
      margin-right: 5px; }
    nav#main-nav h3:hover span {
      max-width: 250px;
      opacity: 1; }
  nav#main-nav ul.links {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-items: center; }
  nav#main-nav .hamburger {
    display: none; }
  @media (max-width: 842px) {
    nav#main-nav .hamburger {
      display: block;
      cursor: pointer;
      width: 48px;
      height: 48px; }
      nav#main-nav .hamburger svg #hamburger .dash.top, nav#main-nav .hamburger svg #hamburger .dash.center, nav#main-nav .hamburger svg #hamburger .dash.bottom {
        transition: all .6s ease; }
      nav#main-nav .hamburger svg #hamburger .dash.center {
        opacity: 1; }
      nav#main-nav .hamburger svg #hamburger .dash.top, nav#main-nav .hamburger svg #hamburger .dash.bottom {
        transform: rotate(0deg) translate(0%, 0%); }
      nav#main-nav .hamburger.toggled svg #hamburger .dash.top {
        transform: rotate(45deg) translate(25%, -25%); }
      nav#main-nav .hamburger.toggled svg #hamburger .dash.center {
        opacity: 0; }
      nav#main-nav .hamburger.toggled svg #hamburger .dash.bottom {
        transform: rotate(-45deg) translate(-50%, 3%); }
    nav#main-nav ul.links {
      position: fixed;
      width: 100%;
      height: calc(100vh - 50px);
      left: -150vw;
      top: 50px;
      text-align: center;
      flex-direction: column;
      background: #0087bd;
      transition: .6s ease; }
      nav#main-nav ul.links li {
        display: block;
        height: 100px; }
        nav#main-nav ul.links li a:hover {
          color: white !important; }
    nav#main-nav ul.expanded {
      left: 0; } }
  nav#main-nav .links a.selected {
    font-weight: bold; }
  nav#main-nav .links li {
    margin: 0 1rem;
    font-size: 1.25rem; }
